<template>
  <div v-loading="loading">
    <p style="color: red;">name3-10nickname5-20sexage18-28phonerate2urlURLattr1attr2attr3</p>
    <p style="color: red;">attr4attr5</p>

    <p>
      <el-button type="success" size="mini" @click="insertEvent"></el-button>
      <el-button type="danger" size="mini" @click="$refs.editable.removeSelecteds()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
      <el-button type="warning" size="mini" @click="validEvent"></el-button>
      <el-button type="warning" size="mini" @click="submitEvent">&</el-button>
      <el-button type="primary" size="mini" @click="getInsertEvent"></el-button>
      <el-button type="primary" size="mini" @click="getUpdateEvent"></el-button>
      <el-button type="primary" size="mini" @click="getRemoveEvent"></el-button>
      <el-button type="primary" size="mini" @click="getAllEvent"></el-button>
    </p>

    <el-editable ref="editable" class="my-table11" stripe border size="medium" height="480" style="width: 100%" :editRules="validRules" :editConfig="{trigger: 'dblclick', showIcon: false, showStatus: false}">
      <el-editable-column type="index" width="55">
        <template slot="head">
          <i class="el-icon-setting" @click="dialogVisible = true"></i>
        </template>
      </el-editable-column>
      <template v-for="(item, index) in columnConfigs">
        <template v-if="item.show">
          <el-editable-column v-if="item.prop === 'attr4'" :key="index" v-bind="item">
            <template slot="edit" slot-scope="scope">
              <el-select v-model="scope.row.attr4" @change="attr4ChangeEvent(scope)">
                <el-option v-for="(item, index) in attr4Options" :key="index" :value="item.value" :label="item.label" :disabled="item.disabled"></el-option>
              </el-select>
            </template>
            <template slot-scope="scope">{{ getAttr4Label(scope.row.attr4) }}</template>
          </el-editable-column>
          <el-editable-column v-else-if="item.prop === 'attr5'" :key="index" v-bind="item">
            <template slot="edit" slot-scope="scope">
              <el-select v-model="scope.row.attr5" @change="attr5ChangeEvent(scope)">
                <el-option v-for="(item, index) in attr5Options" :key="index" :value="item.label" :label="item.label"></el-option>
              </el-select>
            </template>
          </el-editable-column>
          <el-editable-column v-else-if="index === 0" :key="index" v-bind="item">
            <template slot="valid" slot-scope="scope">
              <span class="error-msg"><br>{{ scope.rule.message }}<br><br><a href="https://github.com/xuliangzhan/vue-element-extends" target="_blank">API</a></span>
            </template>
          </el-editable-column>
          <el-editable-column v-else :key="index" v-bind="item"></el-editable-column>
        </template>
      </template>
      <el-editable-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="removeEvent(scope.row)"></el-button>
        </template>
      </el-editable-column>
    </el-editable>

    <el-dialog title="" :visible.sync="dialogVisible" width="300px" @open="openCustomEvent">
      <ul>
        <li v-for="(item, index) in columnConfigs" :key="index">
          <el-checkbox v-model="item.checked">{{ item.label }}</el-checkbox>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetCustomEvent"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveCustomEvent"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox } from 'element-ui'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'
import columnsData from '@/common/json/editable/columns.json'

export default {
  data () {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''))
        } else {
          if (parseInt(value) < 18 || parseInt(value) > 28) {
            callback(new Error('18-28'))
          } else {
            callback()
          }
        }
      }, 50)
    }
    const checkRate = (rule, value, callback) => {
      if (parseInt(value || 0) < 2) {
        callback(new Error('2'))
      } else {
        callback()
      }
    }
    const checkAttr2 = (rule, value, callback) => {
      if (!value || XEUtils.isInteger(Number(value))) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    const checkAttr3 = (rule, value, callback) => {
      if (!value || XEUtils.isFloat(Number(value))) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      loading: false,
      dialogVisible: false,
      columnConfigs: [],
      sexList: [],
      regionList: [],
      typeOptions: Array.from(new Array(5), (v, i) => {
        return {
          label: `${i}`,
          value: `type${i}`,
          disabled: false
        }
      }),
      attr4Options: [],
      attr5Options: [],
      validRules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'change' }
        ],
        nickname: [
          { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' }
        ],
        age: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkAge, trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur' }
        ],
        birthdate: [
          { message: '', trigger: 'blur' }
        ],
        rate: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkRate, trigger: 'blur' }
        ],
        url: [
          { required: true, message: 'URL', trigger: 'change' },
          { pattern: /^((ht|f)tps?):\/\/([\w-]+(\.[\w-]+)*\/)*[\w-]+(\.[\w-]+)*\/?(\?([\w\-.,@?^=%&:/~+#]*)+)?/, message: 'http(s)://xxx.com', trigger: 'change' }
        ],
        attr1: [
          { type: 'number', message: '', trigger: 'change' }
        ],
        attr2: [
          { validator: checkAttr2, trigger: 'change' }
        ],
        attr3: [
          { validator: checkAttr3, trigger: 'change' }
        ]
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.attr4Options = XEUtils.clone(this.typeOptions)
      this.attr5Options = XEUtils.clone(this.typeOptions)
      let sexPromise = this.getSexJSON()
      let regionPromise = this.getRegionJSON()
      this.findList()
      this.getColumnConfigs().then(data => {
        this.columnConfigs = data.map(column => {
          let defaultShow = ['name', 'nickname', 'sex', 'region', 'phone', 'rate', 'attr1', 'attr2', 'attr3', 'attr4', 'attr5'].includes(column.prop)
          column.checked = defaultShow
          column.show = defaultShow
          column.editRender.attrs = {
            placeholder: `${column.label}`
          }
          switch (column.prop) {
            case 'sex':
              column.editRender.options = []
              sexPromise.then(rest => {
                column.editRender.options = rest
              })
              break
            case 'region':
              column.editRender.attrs = {options: []}
              regionPromise.then(rest => {
                column.editRender.attrs.options = rest
              })
              break
            case 'birthdate':
              column.editRender.attrs = {
                type: 'date',
                format: 'yyyy-MM-dd'
              }
              break
            case 'rate':
              column.minWidth = '90'
              column.editRender.type = 'visible'
              break
          }
          return column
        })
      })
    },
    findList () {
      this.loading = true
      this.getDataJSON().then(data => {
        this.$refs.editable.reload(data)
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    attr4ChangeEvent (scope) {
      let list = this.$refs.editable.getRecords()
      this.$refs.editable.updateStatus(scope)
      this.attr4Options.forEach(item => {
        item.disabled = list.some(row => row.attr4 === item.value)
      })
    },
    getAttr4Label (value) {
      let selectItem = this.attr4Options.find(item => item.value === value)
      return selectItem ? selectItem.label : null
    },
    attr5ChangeEvent (scope) {
      let list = this.$refs.editable.getRecords()
      this.$refs.editable.updateStatus(scope)
      this.attr5Options = this.typeOptions.filter(item => !list.some(row => row.attr5 === item.label))
    },
    insertEvent () {
      let row = this.$refs.editable.insert()
      // 
      this.$nextTick(() => this.$refs.editable.validateRow(row).catch(e => e))
    },
    removeEvent (row) {
      this.$refs.editable.remove(row)
    },
    validEvent () {
      this.$refs.editable.validate().then(valid => {
        alert('')
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    submitEvent () {
      this.$refs.editable.validate().then(valid => {
        let { insertRecords, removeRecords, updateRecords } = this.$refs.editable.getAllRecords()
        this.postJSON('url', { insertRecords, removeRecords, updateRecords }).then(data => {
          this.findList()
        })
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    openCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.checked = column.show
      })
    },
    resetCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.checked = true
      })
    },
    saveCustomEvent () {
      this.dialogVisible = false
      this.columnConfigs.forEach(column => {
        column.show = column.checked
      })
    },
    getInsertEvent () {
      let rest = this.$refs.editable.getInsertRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getUpdateEvent () {
      let rest = this.$refs.editable.getUpdateRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getRemoveEvent () {
      let rest = this.$refs.editable.getRemoveRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getSelectedEvent () {
      let rest = this.$refs.editable.getSelecteds()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getAllEvent () {
      let rest = this.$refs.editable.getRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    postJSON (data) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve('')
        }, 300)
      })
    },
    getSexJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(sexData, true)), 100)
      })
    },
    getColumnConfigs () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(columnsData, true)), 100)
      })
    },
    getDataJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve([]), 350)
      })
    },
    getRegionJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(regionData, true)), 200)
      })
    }
  }
}
</script>

<style scoped>
.my-table11 .error-msg {
  display: block;
  color: #fff;
  background-color: red;
  border-radius: 8px;
  font-size: 12px;
  line-height: 1;
  padding: 10px;
  position: absolute;
  top: calc(100% + 10px);
  left: 10px;
  z-index: 9;
}
.my-table11 .error-msg:before {
  content: "";
  position: absolute;
  border: 4px solid;
  top: -8px;
  left: 20%;
  border-color: transparent transparent red transparent;
}
</style>



